import React, {useEffect} from 'react';
import './_resume.scss';

import { StaticQuery, graphql } from 'gatsby';

const Resume = ({data}) => {
  useEffect(() => {
    window.onscroll = function(e) {
      var h = document.documentElement, 
      b = document.body,
      st = 'scrollTop',
      sh = 'scrollHeight';

      const percent =((h[st]||b[st]) / ((h[sh]||b[sh]) - h.clientHeight) * 100);
      document.querySelector('.resume').style.filter = "saturate(" + (100 - parseInt(percent)) + "%)";
      document.body.setAttribute('data-scroll', parseInt(percent));
    };
  }, []);

  return(
      <section className="resume" id="resume" style={{
        backgroundImage: "url('" + require("../../images/s-p-a-c-e.png") + "')"
      }}>
        <div className="container">
            <div className="row">
                <div className="col-12">
                  <div className="card card-tertiary">
                  <div className="planet1" style={{
                    backgroundImage: "url('" + require("../../images/planet1.png") + "')"
                  }}/>
                  <div className="planet2" style={{
                    backgroundImage: "url('" + require("../../images/planet2.png") + "')"
                  }}/>
                      <div className="card-header">
                        <div className="row">
                          <div className="col-12">
                            <h2 className="resume-title">Resume</h2>
                          </div>
                        </div>
                      </div>
                      <div className="row">
                        <div className="col-12">
                          <div className="card-body resume-list">
                            <ul>
                              {data.allDataJson.edges[0].node.resume.map((item, i) => (
                                  <li className="resume-list-item" key={ i }>
                                      {item.title}
                                      <ul>
                                        <li>{item.date}</li>
                                        <li>{item.role}</li>
                                      </ul>
                                  </li>
                              ))}
                              </ul>
                          </div>
                        </div>
                      </div>
                  </div>
                </div>
            </div>
        </div>
      </section>
  ) 
};

export default props => (
  <StaticQuery
    query={graphql`
      query ResumeQuery {
        allDataJson {
          edges {
            node {
              resume {
                title
                role
                date
              }
            }
          }
        }
      }
    `}
    render={data => <Resume data={data} {...props} />}
  />
)
                          
