import React, {useEffect} from 'react';
import {Helmet} from 'react-helmet';
import Resume from '../components/resume';
import Interests from '../components/interests';
import Layout from '../components/layout';

function IndexPage() {

    useEffect(() => {
        window.setTimeout(() => {
            if (typeof clippy !== "undefined") {
                clippy.load('Clippy', function(agent) {
                    agent.moveTo(window.innerWidth - 200, window.innerHeight - 200);
                    agent.show();
                });
            }
        }, 500)
    }, [])
      
    return(
        <>
            <Helmet>
                <link rel="https://gitcdn.xyz/repo/pi0/clippyjs/master/assets/clippy.css"/>
                <script src="https://unpkg.com/jquery@3.2.1"/>
                <script src="https://unpkg.com/clippyjs@latest"/>
            </Helmet>
            <Layout>
                <main>
                    <Resume />
                    <Interests />
                </main>
            </Layout>
        </>
    );
}

export default IndexPage;
